import uCharts from '@/components/u-charts/u-charts.js';
import listCell from '@/components/mix-list-cell';
import {
	mapState
} from 'vuex';

let startY = 0, moveY = 0, pageAtTop = true;
export default {
	components: {
		listCell
	},
	data() {
		return {
			show_data: {},
			contentData: {},
			coverTransform: 'translateY(0px)',
			coverTransition: '0s',
			moving: false,
			wx_user_info: {},
			temp_role: 2,
			TabCur: 0,
			scrollLeft: 0,
			current_nav: 0,
		}
	},
	computed: {
		...mapState(['hasLogin', 'userInfo', 'wx_userinfo', 'role', 'token','let_days'])
	},
	onLoad() {
		uni.showLoading({
			title: '加载中...'
		})
		this.temp_role = this.role;
		this.getContentData();
		var _self = this;
	
	},
	onShow() {},




















	methods: {
		toWhere(url) {
			if (url == '通知') {
				uni.navigateTo({
					url: '/pages/notice/notice'
				})
			} else {
				uni.navigateTo({
					url: '/pages/set/set'
				})
			}
		},

		navToUserInfo() {
			uni.navigateTo({
				url: '/pages/userinfo/userinfo'
			})
		},
		/**
		 * 统一跳转接口,拦截未登录路由
		 * navigator标签现在默认没有转场动画，所以用view
		 */
		entry_admin() {
			uni.navigateTo({
				url: '/pages/user/entry_admin/entry_admin',
				success: res => {}
			});
		},
		entry_staff() {
			uni.navigateTo({
				url: '/pages/user/entry_staff/entry_staff',
				success: res => {},

			});
		},
		getContentData() {
			var _self = this;
			var now_token = _self.token
			uni.request({
				url: _self.global_baseUrl + '/cardinfo/',
				header: {
					'Authorization': ' Token ' + now_token
				},
				method: "GET",
				success: (res) => {
					_self.check_statusCode(res.statusCode)
					res.data.forEach(item => {
						if (_self.temp_role == item.role) {
							_self.contentData = item;
						}
					})
				}
			});
			// if (_self.temp_role == 2) {
				uni.request({
					url: _self.global_baseUrl + '/orders/',
					header: {
						'Authorization': ' Token ' + now_token
					},
					method: "GET",
					success: (res) => {
						_self.check_statusCode(res.statusCode)
						let temp_show_data = {}
						let temp_order_count = res.data.length;
						let temp_order_allPice = 0;
						res.data.forEach(item => {
							temp_order_allPice += parseFloat(item.count_price);
						})
						if (_self.userInfo) {
							temp_show_data = {
								order_count: temp_order_count,
								order_allPice: temp_order_allPice,
								letTime: _self.let_days
							}
						}
						_self.show_data = temp_show_data;
					}
				});
			// }
			uni.hideLoading();
		},
		
		navTo(url) {
			if (!this.hasLogin) {
				url = '/pages/public/login';
			}
			uni.navigateTo({
				url
			})
		},

		/**
		 *  会员卡下拉和回弹
		 *  1.关闭bounce避免ios端下拉冲突
		 *  2.由于touchmove事件的缺陷（以前做小程序就遇到，比如20跳到40，h5反而好很多），下拉的时候会有掉帧的感觉
		 *    transition设置0.1秒延迟，让css来过渡这段空窗期
		 *  3.回弹效果可修改曲线值来调整效果，推荐一个好用的bezier生成工具 http://cubic-bezier.com/
		 */
		coverTouchstart(e) {
			if (pageAtTop === false) {
				return;
			}
			this.coverTransition = 'transform .1s linear';
			startY = e.touches[0].clientY;
		},
		coverTouchmove(e) {
			moveY = e.touches[0].clientY;
			let moveDistance = moveY - startY;
			if (moveDistance < 0) {
				this.moving = false;
				return;
			}
			this.moving = true;
			if (moveDistance >= 80 && moveDistance < 100) {
				moveDistance = 80;
			}

			if (moveDistance > 0 && moveDistance <= 80) {
				this.coverTransform = `translateY(${moveDistance}px)`;
			}
		},
		coverTouchend() {
			if (this.moving === false) {
				return;
			}
			this.moving = false;
			this.coverTransition = 'transform 0.3s cubic-bezier(.21,1.93,.53,.64)';
			this.coverTransform = 'translateY(0px)';
		}
	}
}
